.video_player_dialog {
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  left: 0px;
  top: 0px;
  z-index: 999;
  display: none; }
  .video_player_dialog .close {
    width: 50px;
    cursor: pointer;
    height: 50px;
    font-size: 30px;
    color: #fff;
    line-height: 50px;
    text-align: center;
    position: absolute;
    right: 0;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: center; }
  .video_player_dialog .video_creator_dialog_video {
    position: absolute;
    background: #000;
    left: 50%;
    top: 50%; }
  .video_player_dialog .video_create_dialog_el {
    width: 100%;
    height: 100%; }
